import { dbService, storageService } from "fbase";
import { useState } from "react";
import { deleteDoc, doc, updateDoc } from "firebase/firestore";
import { deleteObject, ref } from "firebase/storage";

const Nweet = ({ nweetObj, isOwner }) => {
  const [editing, setEditing] = useState(false);
  const [newNweet, setNewNweet] = useState(nweetObj.text);
  const onDeleteClick = async () => {
    const ok = window.confirm("삭제하시겠습니까?");
    if (ok) {
      // const data = doc(dbService, `nweets/${nweetObj.id}`);
      // console.log(data);
      deleteDoc(doc(dbService, "nweets", nweetObj.id));
      if (nweetObj.attachmentUrl !== "") {
        const desertRef = ref(storageService, nweetObj.imgFileRoute);
  
        deleteObject(desertRef).then(() => {
          alert(`File deleted successfully`);
        }).catch((error) => {
          alert(error);
        });
      }
    }
  };

  const toggleEditing = () => setEditing((prev) => !prev);

  const onChange = (event) => {
    const {
      target: { value },
    } = event;
    setNewNweet(value);
  };

  const onSubmit = (event) => {
    event.preventDefault();
    updateDoc(doc(dbService, "nweets", nweetObj.id), { text: newNweet });
    setEditing(false);
  };

    return (
        <div>
          {editing ? (
            <>
              <form onSubmit={onSubmit}>
                <input onChange={onChange} value={newNweet} required />
                <input type="submit" value="Update Nweet" />
              </form>
              <button onClick={toggleEditing}>Cancel</button>
            </>
          ) : (
            <>
              <h4>{nweetObj.text}</h4>
              {nweetObj.attachmentUrl && (
                <img src={nweetObj.attachmentUrl} width="50px" height="50px" alt="" />
              )}
              {isOwner && (
                <>
                  <button onClick={onDeleteClick}>Delete Nweet</button>
                  <button onClick={toggleEditing}>Edit Nweet</button>
                </>
              )}
            </>
          )}
        </div>
    );
};

export default Nweet;